.footer {
	position: relative;
	&__top {
		background: #F2F2F2;
		padding-top: 58px;
		padding-bottom: 50px;
		@include tabletlarge {
			background: transparent;
			padding-top: 0px;
			padding-bottom: 0px; }
		.flex-container {
			justify-content: space-between;
			align-items: flex-start;
			@include tabletlarge {
				justify-content: center;
				align-items: center;
				flex-direction: column;
				padding-left: 0px;
				padding-right: 0px; } }

		.footer-menus {
			width: calc(100%/4);
			@include tabletlarge {
				width: 100%;
				background-color: #F2F2F2; }
			h4 {
				font-family: "IstokWeb Bold";
				font-size: 18px;
				line-height: 20px;
				text-transform: uppercase;
				color: #4F4F4F;
				margin-bottom: 20px;
				position: relative;
				cursor: pointer;
				padding-top: 3px;
				transition: all 0.4s;
				&:after {
					content: '\E002';
					font-family: "svg-icons";
					-webkit-font-smoothing: antialiased;
					-moz-osx-font-smoothing: grayscale;
					font-style: normal;
					font-variant: normal;
					font-weight: 400;
					text-decoration: none;
					text-transform: none;
					display: inline-block;
					font-size: 9px;
					color: #4F4F4F;
					line-height: 18px;
					vertical-align: top;
					margin-left: 14px;
					@include tabletlarge {
						content: '\E007';
						font-family: "svg-icons";
						-webkit-font-smoothing: antialiased;
						-moz-osx-font-smoothing: grayscale;
						font-style: normal;
						font-variant: normal;
						font-weight: 400;
						text-decoration: none;
						text-transform: none;
						display: block;
						position: absolute;
						top: 50%;
						right: 10px;
						transform: translate(0%, -50%);
						font-size: 20px;
						color: #333333; } }

				&:hover::after {
					color: #F37032; }
				&:hover {
					color: #F37032; }
				@include tabletlarge {
					margin-bottom: 0px;
					line-height: 75px;
					padding-left: 15px;
					padding-right: 15px; } }
			h4:nth-child(even) {
				@include tabletlarge {
					background-color: #F2F2F2; } }
			h4.active {
				color: #F37032;
				&:after {
					color: #F37032;
					transform: translate(0%, -50%) rotate(180deg); } }
			ul {
				display: block;
				@include tabletlarge {
					display: none;
					padding-left: 15px;
					padding-right: 15px;
					padding-bottom: 35px; }
				li {
					display: block;
					margin-bottom: 20px;
					a {
						font-family: "IstokWeb Regular";
						font-size: 15px;
						line-height: 16px;
						color: #4F4F4F;
						&:hover {
							color: #F37032; } } }
				li:last-child {
					@include tabletlarge {
						margin-bottom: 0px; } } } }
		.footer-menus:nth-child(even) {
				@include tabletlarge {
					background-color: #FFFFFF; } } }
	&__bottom {
		background: #4F4F4F;
		padding-top: 18px;
		padding-bottom: 17px;
		p.right-reserved {
			font-family: "IstokWeb Regular";
			font-size: 15px;
			line-height: 15px;
			color: #FFFFFF;
			font-weight: 400;
			@include ultramobila {
				font-size: 13px; }
			@include ultra-ultra-mobila {
				font-size: 12px; }
			a {
				color: #FEE541;
				&:hover {
					color: #F37032; } } } } }

