div.youtube {
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  display: block;
  transition: all .2s ease-out;
  cursor: pointer;
  background-size: cover;
  z-index: 10;
  max-width: 100%;
  height: 100%;
  width: 100%;
  iframe {
    position: relative;
    z-index: 10;
    width: 100%; } }
.youtube .play {
  background-color: rgba(255, 255, 255, 0.85);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 165px;
  width: 165px;
  border-radius: 20px;
  transition: all .4s ease-out;
  &:before {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-33%, -50%);
    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-bottom-width: 43px;
    border-top-width: 43px;
    border-left: 41px solid #E30613;
    @include tablet {
      border-bottom-width: 23px;
      border-top-width: 23px;
      border-left: 21px solid #E30613; } }
  @include tablet {
    width: 78px;
    height: 78px;
    border-radius: 10px; } }
.youtube:hover .play {
  background-color: rgba(255, 255, 255, 1); }


.recipe-video {
  position: relative;
  height: 558px;
  @include tabletlarge {
    height: 360px; }
  @include tablet {
    height: 265px; } }

.recipe-inst {
  padding-top: 52px;
  position: relative;
  background-color: #fff;
  position: relative;
  z-index: 20;
  @include tablet {
    padding-top: 27px; }
  .flex-container {
    flex-direction: column; }
  .recipe-inst-name {
    font-family: "Gilroy ExtraBold";
    line-height: 40px;
    font-size: 35px;
    color: #4F4F4F;
    text-align: center;
    margin-bottom: 34px;
    @include tablet {
      font-size: 30px;
      margin-bottom: 30px; } }
  .recipe-inst-text {
    font-family: "IstokWeb Regular";
    font-style: normal;
    font-weight: normal;
    line-height: 24px;
    font-size: 18px;
    text-align: center;
    color: #4F4F4F;
    max-width: 580px;
    width: 100%;
    margin: 0px auto 28px; }
  .recipe-inst-icons {
    position: relative;
    max-width: 400px;
    width: 100%;
    margin: 0px auto 35px;
    display: flex;
    justify-content: center;
    justify-content: center;
    align-items: center;
    span {
      display: block;
      color: #4F4F4F;
      position: relative;
      font-family: "IstokWeb Bold";
      font-weight: bold;
      font-size: 15px;
      line-height: 15px;
      transition: color 0.4s;
      margin-right: 20px;
      margin-left: 20px;
      &:before {
        transition: color 0.4s; } }
    span.people-count {
      width: 82px;
      height: 47px;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      &:before {
        width: 82px;
        height: 47px;
        top: 0px;
        background: url("../img/icons-images/people-table-inner.svg") center center no-repeat; }
      &:after {
        display: none; } }

    span.time {
      width: 77px;
      padding-top: 4px;
      padding-left: 0px;
      height: 44px;
      &:before {
        width: 77px;
        height: 37px;
        background: url("../img/icons-images/plate-inner.svg") center center no-repeat; }
      &:after {
        display: none; } } } }

.recipe-inst-ingridients {
  position: relative;
  z-index: 20;
  h4, h5 {
    font-family: "IstokWeb Bold";
    font-style: normal;
    font-weight: bold;
    line-height: 24px;
    font-size: 20px;
    color: #4F4F4F; }
  h4 {
    text-align: center;
    margin-bottom: 28px;
    border-top: 1px solid #4F4F4F;
    padding-top: 33px; }
  h5 {
    text-align: left;
    margin-bottom: 19px; }
  &__inner {
    display: flex;
    background-color: #F2F2F2;
    border-radius: 0px 30px 30px 30px;
    padding: 35px 54px;
    @include tabletlarge {
      flex-direction: column; }
    @include ultramobila {
      padding: 30px 25px; }
    &-right, &-left {
      width: 49.8%;
      @include tabletlarge {
        width: 100%; } }
    &-left {
      border-right: 1px dashed #828282;
      @include tabletlarge {
        border-right: none;
        border-bottom: 1px dashed #828282;
        padding-bottom: 30px; } }
    &-right {
      padding-left: 55px;
      @include tabletlarge {
        padding-left: 0px;
        padding-top: 30px; } }

    .ingridients-count {
      display: flex;
      max-width: 400px;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 12px;
      border-bottom: 1px dashed #828282;
      margin-bottom: 12px;
      @include tabletlarge {
        max-width: 100%; }
      p {
        font-family: "IstokWeb Regular";
        font-style: normal;
        font-weight: normal;
        line-height: 20px;
        font-size: 18px;
        color: #4F4F4F; } }
    .ingridients-count:last-child {
      border-bottom: none;
      margin-bottom: 0px; } } }


.recipe-inst-cooking {
  margin-bottom: 200px;
  position: relative;
  z-index: 20;
  @include tablet {
    margin-bottom: 30px; }
  h4 {
    font-family: "IstokWeb Bold";
    font-style: normal;
    font-weight: bold;
    line-height: 24px;
    font-size: 20px;
    color: #4F4F4F;
    margin-top: 32px;
    margin-bottom: 28px;
    text-align: center; }

  .flex-container {
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    @include tablet {
      flex-direction: column;
      justify-content: center; } }
  &__inner {
    display: block;
    width: 31%;
    position: relative;
    margin-bottom: 38px;
    @include tablet {
      width: 100%; }
    .stage-number {
      position: absolute;
      z-index: 6;
      left: 0px;
      top: 0px;
      border-radius: 0px 10px 10px 10px;
      width: 80px;
      height: 83px;
      text-align: center;
      background-color: #ED1B24;
      font-family: "BebasNeue Regular";
      line-height: normal;
      font-size: 60px;
      color: #FFFFFF;
      display: flex;
      justify-content: center;
      align-items: center; }
    img {
      width: 100%;
      height: auto;
      position: relative;
      z-index: 4;
      display: block;
      margin-bottom: 29px;
      @include tablet {
        margin-bottom: 39px; } }
    p {
      font-family: "IstokWeb Regular";
      font-style: normal;
      font-weight: normal;
      line-height: 22px;
      font-size: 18px;
      color: #4F4F4F;
      text-align: left; } } }

.pepper {
  position: absolute;
  left: -50px;
  bottom: 188px;
  z-index: 10;
  width: 100%;
  max-width: 340px;
  height: auto;
  display: block;
  @include desktop {
    display: none; } }

