	.swiper-wrapper {
		@include desktop {
			height: auto; } } }
.stock-swiper-container .swiper-pagination-bullet {
	width: 22px;
	height: 22px;
	background: none;
	position: relative;
	opacity: 1;
	&:before {
		content: '';
		display: block;
		position: absolute;
		border-radius: 50%;
		width: 12px;
		height: 12px;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background-color: #E0E0E0; }
	canvas {
		position: absolute;
		left: 0px;
		top: 0px;
		opacity: 0; } }
.stock-swiper-container .swiper-pagination-bullet-active {
	canvas {
		opacity: 1; } }
.stock-swiper-container.swiper-container-horizontal>.swiper-pagination-bullets {
	bottom: 0px;
	position: relative;
	margin: 0px auto 10px;
	text-align: center; }

.stock-swiper-container.swiper-container {
	padding-bottom: 50px;
	@include desktop {
		padding-bottom: 0px; } }
.swiper-slide-inner {
	display: flex;
	padding-top: 40px;
	padding-bottom: 40px;
	// @include tabletlarge
	// 	padding-top: 10px
 }	// 	padding-bottom: 10px
.slide-inner__left {
