.partners-intro {
	min-height: 460px;
	position: relative;
	background: url("../img/partners-two.jpg") -90px 7px no-repeat;
	background-size: cover;
	&:before {
		content: '';
		display: block;
		position: absolute;
		height: 7px;
		width: 100%;
		background-color: #F37032; }
	@include desktop {
		min-height: 382px; }
	@include tablet {
		min-height: 264px; }
	.flex-container {
		flex-direction: column;
		position: relative; }
	.heaadline {
		position: relative; }
	h1 {
		font-family: "Gilroy ExtraBold";
		line-height: normal;
		font-size: 150px;
		line-height: 125px;
		color: #FFFFFF;
		position: relative;
		text-align: center;
		margin: 0px;
		z-index: 10;
		@include tabletlarge {
			font-size: 110px;
			line-height: 90px; }
		@include tablet {
			font-size: 90px;
			line-height: 80px; }

		span {
			display: block;
			font-size: 50px;
			line-height: 50px;
			@include tabletlarge {
				font-size: 35px;
				line-height: 35px; }
			@include tablet {
				font-size: 29px;
				line-height: 29px; } } }
	.marker {
		position: relative;
		z-index: 10;
		margin: 70px auto 20px;
		@include desktop {
			margin: 30px auto 20px; }
		@include tabletlarge {
			margin: 30px auto 10px; }
		@include tablet {
			margin: 39px auto 15px; }
		&:before {
			position: relative;
			content: '';
			display: block;
			background: url("../img/marker-partners.png") center center no-repeat;
			background-size: cover;
			width: 51px;
			height: 61px;
			z-index: 5;
			@include tablet {
				width: 33px;
				height: 40px; } }
		&:after {
			content: '';
			position: absolute;
			display: block;
			background: url("../img/marker-shadow.png") center center no-repeat;
			background-size: cover;
			width: 51px;
			height: 61px;
			z-index: 3;
			left: 50%;
			top: 0px;
			transform: translate(-50%, 0%);
			@include tablet {
				width: 33px;
				height: 40px; } } }
	p.headline-shadow {
		position: absolute;
		display: block;
		left: 52.2%;
		top: 0px;
		transform: translate(-50%, 0%);
		font-family: "Gilroy ExtraBold";
		line-height: 150px;
		font-size: 150px;
		color: #20211B;
		mix-blend-mode: multiply;
		opacity: 0.8;
		filter: blur(9px);
		z-index: 5;
		@include tabletlarge {
			font-size: 140px;
			line-height: 140px; }
		@include tablet {
			font-size: 90px;
			line-height: 90px; } }
	p.locale {
		font-family: "IstokWeb Regular";
		font-style: normal;
		font-weight: normal;
		line-height: 48px;
		font-size: 44px;
		color: #FFFFFF;
		text-align: center;
		margin-top: 20px;
		@include tablet {
			font-size: 20px;
			line-height: 22px;
			margin-top: 0px; } } }
.partners {
	position: relative;
	padding-top: 60px;
	h2 {
		font-family: "Gilroy ExtraBold";
		line-height: 38px;
		font-size: 35px;
		color: #4F4F4F;
		text-align: center;
		margin-bottom: 40px;
		@include tablet {
			font-size: 30px;
			line-height: 33px; } }
	.flex-container {
		justify-content: space-around;
		align-items: flex-start;
		flex-wrap: wrap; }
	&__inner {
		width: calc((100%/2 - 46px));
		margin-right: 23px;
		margin-left: 23px;
		position: relative;
		margin-bottom: 46px;
		z-index: 20;
		@include tabletlarge {
			margin-right: 10px;
			margin-left: 10px;
			width: calc((100%/2 - 20px));
			margin-bottom: 40px; }
		@include mobila {
			margin: 0px auto 40px;
			width: 100%; }
		img.partners-image {
			display: block;
			max-width: 100%;
			height: auto;
			position: relative;
			z-index: 17; }
		img.blured-bg {
			display: block;
			position: absolute;
			width: 100%;
			height: calc(100% - 9px);
			z-index: -1;
			top: 9px;
			left: 0px;
			filter: blur(14px); }
		&-info {
			position: absolute;
			bottom: 0px;
			left: 0px;
			width: 100%;
			height: 125px;
			background: rgba(0, 0, 0, 0.7);
			padding-left: 15px;
			padding-right: 15px;
			overflow: hidden;
			transition: all 0.4s;
			cursor: pointer;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			z-index: 20;
			@include tabletlarge {
				height: 90px; }
			@include tablet {
				height: 58px; }
			h3 {
				text-align: center;
				font-family: "IstokWeb Regular";
				font-style: normal;
				font-weight: 500;
				font-size: 45px;
				line-height: 48px;
				color: #FFFFFF;
				position: absolute;
				width: 100%;
				padding-left: 10px;
				padding-right: 10px;
				transition: all 0.3s;
				margin-bottom: 20px;
				@include desktop {
					font-size: 30px;
					line-height: 34px;
					margin-bottom: 10px; }
				@include tablet {
					font-size: 20px;
					line-height: 24px; }
				@include mobila {
					margin-bottom: 30px; }
				@include ultramobila {
					margin-bottom: 20px; }
				@include ultra-ultra-mobila {
					margin-bottom: 10px; } }
			p {
				opacity: 0;
				position: relative;
				font-family: "IstokWeb Regular";
				font-weight: 300;
				line-height: 25px;
				font-size: 18px;
				text-align: center;
				color: #FFFFFF;
				transition: all 0s;
				width: 100%;
				padding-left: 30px;
				padding-right: 30px;
				@include desktop {
					padding-right: 10px;
					padding-left: 10px;
					font-size: 15px;
					line-height: 19px; }
				@include tablet {
					display: none; } }

			a {
				opacity: 0;
				position: relative;
				width: 248px;
				display: block;
				height: 63px;
				font-family: "IstokWeb Bold";
				font-style: normal;
				font-weight: bold;
				font-size: 18px;
				line-height: 63px;
				text-align: left;
				text-transform: uppercase;
				color: #F37032;
				border: 1px solid #F37032;
				border-radius: 20px 0px 20px 0px;
				padding-left: 27px;
				margin: 40px auto 10px;
				transition: color 0.4s, background-color 0.4s, opacity 0s;
				@include tabletlarge {
					margin: 10px auto 5px;
					font-size: 15px;
					height: 53px;
					line-height: 53px;
					width: 230px; }
				&:hover {
					background-color: #F37032;
					color: #FFFFFF; }
				&:after {
					content: '\E002';
					font-family: "svg-icons";
					-webkit-font-smoothing: antialiased;
					-moz-osx-font-smoothing: grayscale;
					font-style: normal;
					font-variant: normal;
					font-weight: 400;
					text-decoration: none;
					text-transform: none;
					display: block;
					position: absolute;
					top: 50%;
					right: 27px;
					transform: translate(0%, -50%);
					font-size: 9px;
					color: #F37032; }
				&:hover::after {
					color: #FFFFFF; } } } }
	&__inner.active-element {
		.partners__inner-info {
			height: 100%;
			h3 {
				position: relative; }
			p {
				opacity: 1; }
			a {
				opacity: 1; } } }
	.get-info h2 {
		margin-top: 50px;
		margin-bottom: 67px;
		@include tabletlarge {
			margin-top: 10px;
			margin-bottom: 60px; } }
	img.basil-image {
		display: none;
		@include desktopfull {
			display: block; } } }

