$icon_font_name:"svg-icons";
@font-face {
    font-family:$icon_font_name;
    src:
            url('../fonts/' + $icon_font_name + '.woff') format('woff'),
            url('../fonts/' + $icon_font_name + '.ttf') format('truetype');
}
.icon-address,
.icon-arrow-footer,
.icon-boild,
.icon-cake,
.icon-close,
.icon-count,
.icon-down-arrow,
.icon-letter,
.icon-organic,
.icon-people-table,
.icon-plate,
.icon-salate,
.icon-telegram,
%last_selector {
           &:before {
               font-family:$icon_font_name;
               -webkit-font-smoothing: antialiased;
               -moz-osx-font-smoothing: grayscale;
               font-style: normal;
               font-variant: normal;
               font-weight: normal;
               // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
               text-decoration: none;
               text-transform: none;
           }
       }

%icons_before_after {
    &:before,
    &:after {
        font-family: $icon_font_name;
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

%icons_after {
    &:after {
        font-family: $icon_font_name;
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

.icon-address:before {
    content:'\E001';
}
.icon-arrow-footer:before {
    content:'\E002';
}
.icon-boild:before {
    content:'\E003';
}
.icon-cake:before {
    content:'\E004';
}
.icon-close:before {
    content:'\E005';
}
.icon-count:before {
    content:'\E006';
}
.icon-down-arrow:before {
    content:'\E007';
}
.icon-letter:before {
    content:'\E008';
}
.icon-organic:before {
    content:'\E009';
}
.icon-people-table:before {
    content:'\E00A';
}
.icon-plate:before {
    content:'\E00B';
}
.icon-salate:before {
    content:'\E00C';
}
.icon-telegram:before {
    content:'\E00D';
}