.stocks-page main {
	position: relative;
	overflow: hidden; }
.slider-stocks {
	border-top: 7px solid #F37032;
	@include tablet {
		border-width: 3px;
		min-height: 265px; } }
.stock-swiper {
	position: relative; }
.stock-slide {
	margin-bottom: 10px;
	img {
		display: block;
		border: 10px solid #ED1B24;
		margin: 0px auto; }
	.slide-config {
		max-width: 500px;
		width: 100%;
		display: flex;
		min-height: 70px;
		margin: 10px auto 0px;
		background-color: black;
		border-radius: 10px 10px 0px 0px; } }
.stocks-loadmore {
	display: block;
	font-family: "IstokWeb Bold";
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 18px;
	text-transform: uppercase;
	color: #F37032;
	background: transparent;
	max-width: 308px;
	width: 100%;
	height: 63px;
	line-height: 63px;
	position: relative;
	border: 1px solid #F37032;
	border-radius: 10px 0px 10px 0px;
	padding-left: 25px;
	text-align: left;
	margin: 71px auto 20px;
	@include tablet {
		margin: 20px auto 20px; }

	&:after {
		content: '\E002';
		font-family: "svg-icons";
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		font-style: normal;
		font-variant: normal;
		font-weight: 400;
		text-decoration: none;
		text-transform: none;
		display: block;
		position: absolute;
		top: 50%;
		right: 4%;
		transform: translate(-5%, -50%);
		font-size: 9px;
		color: #F37032; }
	&:hover {
		color: #fff;
		background: #F37032; }
	&:hover::after {
		color: #fff; } }

