img.plate-stocks {
	position: absolute;
	left: 0px;
	bottom: 190px;
	z-index: 9;
	display: block;
	@include desktopcontainerlarge {
		max-width: 270px; }
	@include desktopfull {
		display: none; } }

.stock-animation-trigger {
	position: absolute;
	bottom: 1500px;
	left: 50%;
	transform: translate(-50%, 0%);
	width: 1px;
	height: 1px;
	opacity: 0;
	z-index: -1; }

.swiper-thumbnails {
	display: flex;
	justify-content: center;
	align-items: center;
	max-width: 1200px;
	width: 100%;
	margin: 0px auto;
	background-color: #4F4F4F;
	border-radius: 10px;
	div {
		margin: 0px 10px;
		font-size: 0px;
		cursor: pointer;
		position: relative;
		transition: all 0.4s; }
	img {
		max-width: 100px; }
	div.is-active {
		transform: scale(1.2); }
	div:hover {
		transform: scale(1.2); } }

.swiper-stocks-navigation {
	div {
		position: absolute;
		top: 50%;
		transform: translate(0%, -50%);
		background-size: cover;
		width: 40px;
		height: 40px; } }
.swiper-button-next {
	background: url("../img/icons-images/chevron-right.svg") center center no-repeat; }
.swiper-button-prev {
	background: url("../img/icons-images/chevron-left.svg") center center no-repeat; }
