.recipe-intro {
	min-height: 538px;
	border-top: 7px solid #F37032;
	background: url("../img/recipe-page-images/recipe-intro-bg.jpg") top center no-repeat;
	background-size: cover;
	position: relative;

	@include tabletlarge {
		min-height: 300px; }
	@include tablet {
		border-width: 3px;
		min-height: 265px; }
	.flex-container {
		flex-direction: column; }
	.author-image {
		margin: 70px auto 0px;
		max-width: 300px;
		width: 100%;
		@include tablet {
			margin: 22px auto 0px; }
		img {
			display: block;
			position: relative;
			margin: 0px auto;
			width: 214px;
			height: auto;
			border-radius: 50%;
			@include tablet {
				width: 83px; } } }
	.author-name {
		margin-top: 50px;
		margin-bottom: 28px;
		@include tablet {
			margin-top: 15px;
			margin-bottom: 10px; }
		p {
			font-family: "IstokWeb Bold";
			font-style: normal;
			font-weight: bold;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			color: #4F4F4F;
			@include tablet {
				font-size: 16px;
				line-height: 20px; } } }
	.author-text {
		width: 100%;
		max-width: 400px;
		margin: 0px auto 70px;
		@include tablet {
			max-width: 310px; }
		p {
			font-family: "IstokWeb Italic";
			font-style: italic;
			font-weight: normal;
			font-size: 18px;
			text-align: center;
			line-height: 22px;
			width: 100%;
			color: #4F4F4F;
			@include tablet {
				font-size: 16px;
				line-height: 20px; } } } }

.recipes-section {
	padding-top: 70px;
	position: relative;
	background-color: #fff;
	@include tablet {
		padding-top: 27px; }
	.recipes-category {
		.flex-container {
			flex-direction: column;
			@include ultramobila {
				padding: 0px; } }
		h3 {
			font-family: "Gilroy ExtraBold";
			line-height: normal;
			font-size: 35px;
			color: #4F4F4F;
			line-height: 38px;
			position: relative;
			text-align: center;
			margin-bottom: 72px;
			cursor: pointer;
			@include tablet {
				font-size: 30px;
				line-height: 33px;
				margin-bottom: 39px; }
			&:after {
				content: '';
				margin-left: 14px;
				display: inline-block;
				position: relative;
				top: -2px;
				width: 0;
				height: 0;
				border: 0 solid transparent;
				border-bottom-width: 8px;
				border-top-width: 8px;
				border-left: 8px solid #4F4F4F;
				@include tablet {
					margin-left: 6px;
					top: 0px; } } }
		h3.active-category__selector {
			&:after {
				transform: rotate(90deg); } }
		.recipes-category__list {
			display: none;
			ul {
				display: flex;
				flex-wrap: wrap;
				justify-content: flex-start;
				align-items: flex-start;
				margin-bottom: 82px;
				padding-left: 8px;
				padding-right: 7px;
				position: relative;
				z-index: 40;
				@include tablet {
					margin-bottom: 30px; }
				@include ultramobila {
					padding: 0px 16px; } }

			li {
				margin-left: 10px;
				margin-right: 10px;
				padding: 17px 14px 14px;
				border: 1px solid #4F4F4F;
				border-radius: 10px 0px 10px 0px;
				margin-bottom: 18px;
				background-color: #fff;
				cursor: pointer;
				transition: all 0.4s;
				@include ultramobila {
					margin-left: 5px;
					margin-right: 5px;
					padding: 10px 12px; }
				span {
					font-family: "IstokWeb Regular";
					font-style: normal;
					font-weight: normal;
					line-height: 20px;
					font-size: 20px;
					color: #4F4F4F;
					transition: inherit;
					@include tablet {
						font-size: 16px;
						line-height: 16px; } }
				&:hover {
					border-color: #F37032; }
				&:hover span {
					color: #F37032; } }
			li.active-category {
				border-color: #F37032;
				span {
					color: #F37032; } } } } }

.recipes-container {
	position: relative;
	z-index: 40; }

.recipes-loadmore {
	display: block;
	font-family: "IstokWeb Bold";
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 18px;
	text-transform: uppercase;
	color: #F37032;
	background: transparent;
	max-width: 308px;
	width: 100%;
	height: 63px;
	line-height: 63px;
	position: relative;
	border: 1px solid #F37032;
	border-radius: 10px 0px 10px 0px;
	padding-left: 25px;
	text-align: left;
	margin: 0px auto 20px;
	@include tablet {
		margin: 49px auto 23px; }
	&:after {
		content: '\E001';
		font-family: "svg-icons";
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		font-style: normal;
		font-variant: normal;
		font-weight: 400;
		text-decoration: none;
		text-transform: none;
		display: block;
		position: absolute;
		top: 50%;
		right: 4%;
		transform: translate(-5%, -50%);
		font-size: 9px;
		color: #F37032; }
	&:hover {
		color: #fff;
		background: #F37032; }
	&:hover::after {
		color: #fff; } }


.recipes-list {
	.flex-container {
		justify-content: space-around;
		align-items: flex-start;
		flex-wrap: wrap; }
	.recipe-item {
		position: relative;
		display: block;
		width: 31.93%;
		height: 699px;
		margin-right: 0.7%;
		margin-left: 0.7%;
		margin-bottom: 66px;
		z-index: 7;
		@include tabletlarge {
			width: 44%;
			margin-right: 3%;
			margin-left: 3%;
			margin-bottom: 40px; }
		@include tablet {
			margin-bottom: 20px;
			height: 446px; }
		@include mobila {
			width: 46%;
			margin-left: 2%;
			margin-right: 2%; }
		@include ultramobila {
			width: 100%;
			margin: 0px auto 30px; } }
	.recipes-item-info {
		min-height: 117px;
		width: 100%;
		position: absolute;
		bottom: 0px;
		left: 0px;
		padding-left: 10px;
		padding-right: 10px;
		background-color: rgba(0, 0, 0, 0.7);
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column; }
	.recipe-name {
		font-family: "IstokWeb Bold";
		font-style: normal;
		font-weight: bold;
		font-size: 18px;
		line-height: 20px;
		text-align: center;
		color: #FFFFFF;
		margin-bottom: 20px;
		position: relative;
		transition: all 0.4s; }
	.recipes-info-icons {
		display: flex;
		justify-content: center;
		align-items: center;
		span {
			display: block;
			color: #FFFFFF;
			position: relative;
			font-family: "IstokWeb Bold";
			font-weight: bold;
			font-size: 15px;
			line-height: 15px;
			transition: color 0.4s;
			margin-right: 13px;
			margin-left: 13px;
			&:before {
				transition: all 0.4s; } } } }

.recipes-list .recipe-item:hover .recipe-name, .recipes-list .recipe-item:hover .recipes-info-icons span {
	color: #F37032; }



