p, h1, h2, h3, h4, h5 h6, a, span, div, li {
	&::selection {
		color: white;
		background-color: #71b1ff; } }
a, button {
	user-select: none;
	outline: none; }

span.sp-links {
	cursor: pointer; }

html {
	height: 100%;
	width: 100%;
	position: relative; }


body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.75;
	overflow-x: hidden;
	opacity: 1;
	margin: 0px;
	box-sizing: border-box;
	// display: flex
	// min-height: 100vh
	// flex-direction: column
	background-color: #fff; }
h1,p.headlines,h3,h4,h5,p {
	margin: 0px;
	display: block; }
.img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }
div.overlay {
	overflow: hidden; }
section, div, p, h1, p.headlines, h3, h4, h5, h6, span, a {
	box-sizing: border-box; }
a, button, *:after, *:before {
	-webkit-transition: all 0.4s;
	transition: all 0.4s; }
a {
	text-decoration: none;
	outline: none;
	cursor: pointer; }
.container {
	max-width: 1280px;
	width: 100%;
	margin: 0px auto;
	&:after {
	    content: "";
	    display: block;
	    clear: both;
	    visibility: hidden;
	    line-height: 0;
	    height: 0;
	    margin: 0;
	    padding: 0; } }
