@media (max-width: 992px) { .r-centers-block p { font-size: 16px; line-height: 20px; } }

.r-centers-block p span { display: block; font-family: "IstokWeb Bold"; font-weight: bold; margin-bottom: 10px; }

.r-centers-block p:nth-child(2)::before { content: ''; display: block; width: 19px; height: 39px; position: absolute; top: -10px; left: 0px; background: url("../img/onion-icon.png") center center no-repeat; background-size: cover; }

.r-centers-block p:nth-child(3)::before { content: ''; display: block; width: 19px; height: 25px; position: absolute; top: -2px; left: 0px; background: url("../img/contact-supl-marker.png") center center no-repeat; background-size: cover; }

.r-centers-block p:nth-child(4)::before { content: ''; display: block; width: 32px; height: 22px; position: absolute; top: -2px; left: 0px; background: url("../img/basket-icon.png") center center no-repeat; background-size: cover; }

.r-centers-block p:nth-child(5)::before { content: ''; display: block; width: 24px; height: 28px; position: absolute; top: -5px; left: 0px; background: url("../img/clock-icon.png") center center no-repeat; background-size: cover; }

.r-centers-block p:nth-child(6)::before { content: ''; display: block; width: 21px; height: 21px; position: absolute; top: -2px; left: 0px; background: url("../img/adv-phone.png") center center no-repeat; background-size: cover; }

.r-centers-block:last-child { border: none; margin-bottom: 0px; }

.our-offices-headline { color: #333333; font-size: 25px; font-weight: bold; font-family: "IstokWeb Bold"; line-height: 29px; text-align: center; margin: 42px auto 38px; }

@media (max-width: 768px) { .our-offices-headline { display: none; } }

.our-offices-wrap { display: flex; justify-content: space-between; align-items: flex-start; flex-wrap: wrap; }

.our-offices__inner { width: calc((100%/4) - 40px); margin-left: 20px; margin-right: 20px; }

@media (max-width: 992px) { .our-offices__inner { width: 43%; margin-bottom: 30px; } }

@media (max-width: 610px) { .our-offices__inner { width: 100%; } }

.our-offices__inner a { display: block; color: #333333; }

.our-offices__inner a:hover { color: #f37032; }

.our-offices-place { color: #4F4F4F; font-size: 18px; line-height: 25px; font-weight: bold; font-family: "IstokWeb Bold"; text-align: left; }

@media (max-width: 610px) { .our-offices-place { text-align: center; } }

.our-offices-adress { color: #4F4F4F; font-size: 18px; line-height: 22px; font-weight: 400; font-family: "IstokWeb Regular"; }

@media (max-width: 610px) { .our-offices-adress { text-align: center; } }

.our-offices-wrap { margin-bottom: 130px; }

@media (max-width: 992px) { .our-offices-wrap { margin-bottom: 20px; } }

.contacts-map { height: 470px; border-top: 7px solid #F37032; width: 100%; position: relative; margin-bottom: 50px; }

@media (max-width: 992px) { .contacts-map { display: none; } }

.contacts-map #contactMap { width: 100%; height: 100%; }
