.address {
	height: 880px;
	border-top: 7px solid #F37032;
	border-bottom: 7px solid #F37032;
	position: relative;
	display: flex;
	@include tablet {
		height: auto;
		min-height: 500px;
		flex-direction: column; }
	&__left {
		width: 365px;
		height: 100%;
		background-color: #ED1B24;
		position: relative;
		padding-top: 20px;
		padding-bottom: 20px;
		@include tablet {
			width: 100%;
			min-height: 200px; }

		.address-city__chose {
			font-family: "SegoeUIBold";
			line-height: 21px;
			font-size: 18px;
			font-weight: bold;
			color: #FFFFFF;
			position: relative;
			padding-left: 33px;
			cursor: pointer;
			@include ultramobila {
				font-size: 15px;
				line-height: 18px; }
			&:before {
				left: 0px;
				top: -4px;
				position: absolute;
				display: inline-block;
				content: '';
				width: 22px;
				height: 30px;
				background: url("../img/icons-images/address.svg") center center no-repeat;
				background-size: cover; }
			&:after {
				content: '';
				display: inline-block;
				position: relative;
				top: 0px;
				width: 0;
				height: 0;
				margin-left: 8px;
				border: 0 solid transparent;
				border-bottom-width: 6px;
				border-top-width: 6px;
				border-left: 5px solid #FFFFFF; } }
		.address-city__chose.list-active {
			&:after {
				transform: rotate(90deg); } }
		&-chose {
			padding-top: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid #FFFFFF;
			padding-left: 19px;
			padding-right: 5px; }
		.full-city-list {
			display: none;
			padding-top: 15px;
			padding-left: 33px;
			li {
				display: block;
				width: 100%;
				margin-bottom: 14px;
				span {
					font-family: "SegoeUIRegular";
					line-height: 22px;
					font-size: 18px;
					color: #FFFFFF;
					display: block;
					width: 100%;
					text-align: left;
					cursor: pointer;
					@include ultramobila {
						font-size: 15px;
						line-height: 18px; } } }
			li:last-child {
				margin-bottom: 0px; } }
		&-near {
			padding-top: 22px;
			padding-left: 19px; }
		&-near__link {
			display: block;
			position: relative;
			font-family: "SegoeUIRegular";
			font-size: 18px;
			line-height: 22px;
			color: #FFFFFF;
			margin-bottom: 60px;
			padding-left: 33px;
			cursor: pointer;
			@include ultramobila {
				margin-bottom: 30px; }
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -3px;
				top: -3px;
				width: 25px;
				height: 25px;
				background: url("../img/teleg.png") center center no-repeat;
				background-size: cover; } }
		.search-form {
			padding-right: 16px;
			width: 100%;
			display: flex;
			@include tablet {
				margin-bottom: 30px; }
			.form-inner {
				width: calc(100% - 40px);
				height: 49px;
				position: relative; }
			input {
				width: 100%;
				height: 100%;
				display: block;
				background-color: #FFFFFF;
				border-radius: 15px 0px 0px 0px;
				padding-left: 13px;
				padding-right: 13px;
				font-family: "IstokWeb Regular";
				color: #4F4F4F;
				font-weight: 400;
				font-size: 20px; }
			label {
				position: absolute;
				font-family: "IstokWeb Regular";
				font-size: 18px;
				line-height: 18px;
				color: #828282;
				left: 13px;
				top: 50%;
				transform: translate(0%,-50%);
				opacity: 1;
				transition: all .5s;
				cursor: text; }
			button {
				position: relative;
				width: 40px;
				height: 49px;
				background: #FFFFFF;
				border-radius: 0px 0px 15px 0px;
				display: flex;
				justify-content: center;
				align-items: center;
				svg {
					display: block;
					width: 18px;
					height: 18px; } }
			.form-inner.active-field label {
				opacity: 0;
				transform: translate(50%, -50%); } } }




	&__right {
		width: calc(100% - 365px);
		height: 100%;
		position: relative;
		@include tablet {
			width: 100%;
			height: 400px; } } }

#addressMap {
	width: 100%;
	height: 100%; }

.infowindow {
	padding-top: 20px;
	padding-bottom: 20px;
	padding-left: 10px;
	padding-right: 0px;
	width: 100%;
	max-width: 350px;
	h3 {
		font-family: "IstokWeb Regular";
		font-size: 18px;
		line-height: 21px;
		color: #333333;
		margin-bottom: 14px; }
	p {
		font-family: "IstokWeb Regular";
		font-size: 18px;
		line-height: 21px;
		color: #333333;
		margin-bottom: 14px; }
	.worktime {
		position: relative;
		padding-left: 50px;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0px;
			top: 50%;
			transform: translate(0%, -50%);
			width: 32px;
			height: 38px;
			background: url("../img/watch.svg") center center no-repeat;
			background-size: cover; }
		p {
			margin-bottom: 5px; }
		p:last-child {
			color: #DD0A0A; } } }

.gm-style-iw {
	background-color: #FFFFFF;
	border: 1px solid #DD0A0A;
	border-radius: 0px 8px 8px 8px;
	padding-left: 11px; }
.element-outer {
	z-index: 4;
	height: 100%;
	div {
		display: none; }
	&:before {
		content: '';
		display: block;
		width: 0;
		height: 0;
		border: 0 solid transparent;
		border-right-width: 0px;
		border-left-width: 45px;
		border-bottom: 32px solid #FFFFFF;
		position: absolute;
		bottom: 0px;
		left: 60px;
		transform: rotate(-30deg);
		z-index: 3; } }
.custom-close-map {
	background: url("../img/icons-images/close.svg") center center no-repeat;
	background-size: cover; }
